'use client';
import React, { useState } from 'react';
import { HashRouter as Router, Routes, Route, Link, useLocation, Navigate } from 'react-router-dom';
import { usePharmaStore } from './presentation/store/useStore';
import {
    LayoutDashboard,
    ShoppingCart,
    Truck,
    Users,
    Clock,
    Settings,
    LogOut,
    Menu,
    X,
    ChevronLeft,
    ChevronRight
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

// Pages
import LandingPage from './presentation/pages/LandingPage';
import POSMainScreen from './presentation/components/POSMainScreen';
import SupplyChainPage from './presentation/pages/SupplyChainPage';
import QueueKioskPage from './presentation/pages/QueueKioskPage';
import AccessControlPage from './presentation/pages/AccessControlPage';
import HRPage from './presentation/pages/HRPage';
import SettingsPage from './presentation/pages/SettingsPage';

const SidebarLayout = ({ children }: { children: React.ReactNode }) => {
    const { user, logout } = usePharmaStore();
    const location = useLocation();
    const [isCollapsed, setIsCollapsed] = useState(false);

    const MENU_ITEMS = [
        { icon: ShoppingCart, label: 'Punto de Venta', path: '/pos' },
        { icon: Truck, label: 'Abastecimiento', path: '/supply' },
        { icon: Users, label: 'RR.HH.', path: '/hr' },
        { icon: Settings, label: 'Configuración', path: '/settings' },
    ];

    if (!user) return <Navigate to="/" />;

    return (
        <div className="flex h-screen bg-slate-100 overflow-hidden">
            {/* Sidebar */}
            <motion.aside
                initial={{ width: 280 }}
                animate={{ width: isCollapsed ? 80 : 280 }}
                className="bg-slate-900 text-white flex flex-col shadow-2xl z-20 relative"
            >
                <div className="p-6 flex items-center justify-between">
                    {!isCollapsed && (
                        <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
                            <h1 className="font-extrabold text-xl tracking-tight">Pharma<span className="text-cyan-400">Suit</span></h1>
                            <p className="text-xs text-slate-500">v2.1 Enterprise</p>
                        </motion.div>
                    )}
                    <button
                        onClick={() => setIsCollapsed(!isCollapsed)}
                        className="p-2 rounded-lg hover:bg-slate-800 text-slate-400 hover:text-white transition"
                    >
                        {isCollapsed ? <ChevronRight size={20} /> : <ChevronLeft size={20} />}
                    </button>
                </div>

                <nav className="flex-1 px-4 space-y-2 mt-4">
                    {MENU_ITEMS.map((item) => {
                        const isActive = location.pathname === item.path;
                        return (
                            <Link
                                key={item.path}
                                to={item.path}
                                className={`flex items-center p-3 rounded-xl transition-all group relative
                                    ${isActive
                                        ? 'bg-cyan-600 text-white shadow-lg shadow-cyan-900/50'
                                        : 'text-slate-400 hover:bg-slate-800 hover:text-white'
                                    }`}
                            >
                                <item.icon size={24} className={`${isActive ? 'text-white' : 'text-slate-400 group-hover:text-white'} transition-colors`} />
                                {!isCollapsed && (
                                    <span className="ml-3 font-bold text-sm">{item.label}</span>
                                )}
                                {isCollapsed && (
                                    <div className="absolute left-full ml-4 px-2 py-1 bg-slate-800 text-white text-xs rounded opacity-0 group-hover:opacity-100 pointer-events-none whitespace-nowrap z-50">
                                        {item.label}
                                    </div>
                                )}
                            </Link>
                        );
                    })}
                </nav>

                <div className="p-4 border-t border-slate-800">
                    <div className={`flex items-center ${isCollapsed ? 'justify-center' : 'gap-3'}`}>
                        <div className="w-10 h-10 rounded-full bg-gradient-to-br from-cyan-500 to-blue-600 flex items-center justify-center font-bold text-white shadow-lg">
                            {user.name.charAt(0)}
                        </div>
                        {!isCollapsed && (
                            <div className="flex-1 overflow-hidden">
                                <h4 className="font-bold text-sm truncate">{user.name}</h4>
                                <p className="text-xs text-slate-500 truncate">{user.role}</p>
                            </div>
                        )}
                        {!isCollapsed && (
                            <button onClick={logout} className="text-slate-500 hover:text-red-400 transition">
                                <LogOut size={20} />
                            </button>
                        )}
                    </div>
                </div>
            </motion.aside>

            {/* Main Content */}
            <main className="flex-1 overflow-hidden relative">
                {children}
            </main>
        </div>
    );
};

const App: React.FC = () => {
    const { syncData } = usePharmaStore();

    React.useEffect(() => {
        syncData();
    }, []);

    return (
        <Router>
            <Routes>
                {/* Public / Fullscreen Routes */}
                <Route path="/" element={<LandingPage />} />
                <Route path="/kiosk" element={<QueueKioskPage />} />
                <Route path="/access" element={<AccessControlPage />} />

                {/* Protected Routes with Sidebar */}
                <Route path="/pos" element={<SidebarLayout><POSMainScreen /></SidebarLayout>} />
                <Route path="/supply" element={<SidebarLayout><SupplyChainPage /></SidebarLayout>} />
                <Route path="/hr" element={<SidebarLayout><HRPage /></SidebarLayout>} />
                <Route path="/settings" element={<SidebarLayout><SettingsPage /></SidebarLayout>} />

                {/* Fallback */}
                <Route path="*" element={<Navigate to="/" />} />
            </Routes>
        </Router>
    );
};

export default App;
