'use server';

import { query } from '../lib/db';
import { InventoryBatch, EmployeeProfile } from '../domain/types';

export async function fetchInventory(): Promise<InventoryBatch[]> {
    try {
        const res = await query('SELECT * FROM productos');

        // Map DB columns to Domain Type
        // Assuming DB columns might be snake_case or slightly different. 
        // We map what we can and default the rest for safety.
        return res.rows.map((row: any) => ({
            id: row.id?.toString() || `PROD-${Math.random()}`,
            sku: row.sku || row.codigo || 'UNKNOWN',
            name: row.nombre || row.name || 'Sin Nombre',
            dci: row.dci || row.principio_activo || '',
            lot_number: row.lote || 'L-DEFAULT',
            expiry_date: row.vencimiento ? new Date(row.vencimiento).getTime() : Date.now() + 31536000000,
            category: row.categoria === 'MEDICAMENTO' ? 'MEDICAMENTO' : 'RETAIL_BELLEZA', // Simple mapping
            sale_condition: row.condicion_venta || 'VD',
            storage_condition: row.condicion_almacenamiento || 'AMBIENTE',
            allows_commission: row.permite_comision || false,
            active_ingredients: row.principios_activos ? (Array.isArray(row.principios_activos) ? row.principios_activos : [row.principios_activos]) : [],
            is_bioequivalent: row.es_bioequivalente || false,
            stock_actual: Number(row.stock) || 0,
            stock_min: Number(row.stock_min) || 5,
            price: Number(row.precio) || 0,
            supplier_id: row.proveedor_id || 'SUP-001'
        }));
    } catch (error) {
        console.error('Error fetching inventory:', error);
        return [];
    }
}

export async function fetchEmployees(): Promise<EmployeeProfile[]> {
    try {
        const res = await query('SELECT * FROM users');

        return res.rows.map((row: any) => ({
            id: row.id?.toString() || `EMP-${Math.random()}`,
            rut: row.rut || 'UNKNOWN',
            name: row.nombre || row.name || 'Sin Nombre',
            role: row.rol || 'CASHIER', // Default to safest role
            access_pin: row.pin || '0000',
            labor_data: {
                base_salary: Number(row.sueldo_base) || 500000,
                afp: row.afp || 'Modelo',
                isapre: row.isapre || 'Fonasa'
            },
            status: row.estado || 'ACTIVE'
        }));
    } catch (error) {
        console.error('Error fetching employees:', error);
        return [];
    }
}
